.article {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
}

.article-icon {
    display: flex;
    justify-content: flex-end;
    padding-right: 10px;
    top: 28px;
    position: relative;
    left: 83px;
}

.article-icon-cart {
    width: 25px;
    height: 25px;
    color: #aab8d3;
    cursor: pointer;
}

.article-img {
    background-color: #ffa8b747;
    display: flex;
    padding: 20px;
}

.article-img img {
    max-width: 150px;
    object-fit: scale-down;
    aspect-ratio: 1/1;
    border-radius: 2px 2px 0 0;
}

.article-detaills {
    padding: 15px 2px 4px;
    background-color: #252323;
    width: 190px;
    padding-bottom: 15px;
}

.article-name {
    font-size: 16px;
    color: #aba8ab;
    font-weight: 600;
}

.article-price {
    font-size: 25px;
    color: #ff0060;
    font-weight: 700;
}

.article-buttons {
    display: flex;
    justify-content: space-around;
}

.btn-shop {
    display: flex;
    justify-content: center;
}