.navbar {
    transition: 4s ease-in-out all;
}

.navbar-principal {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 15px;
    padding-right: 15px;
    padding-bottom: 25px;
    height: 50px;
    background-color: #303030;
    transition: 4s ease-in-out display;
}

.navbar .navbar-logo {
    font-size: 30px;
    font-weight: 600;
    color: red;
    width: 40%;
}

.navbar .navbar-logo span {
    color: rgb(216, 216, 216);
}

.navbar-pages {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.navbar-pages .link {
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    color: #ddd;
    position: relative;
    text-transform: uppercase;
    user-select: none;
}

.navbar-pages .link.active {
    color: rgb(235, 25, 200);
    /* font-size: 18px; */
    scale: 18px;
}

.navbar-search {
    width: 100%;
}

.navbar-additional {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 20%;
}

.navbar-additional .link.icon {
    position: relative;
}

.navbar-additional .link.icon-shopping::before {
    content: "99";
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: rgb(221, 7, 7);
    color: #ddd;
    text-align: center;
    pad: 2px;
    top: -10px;
    left: 10px;
    position: absolute;
}

.navbar-additional .link.icon svg {
    width: 30px;
    height: 30px;
    color: #ddd;
}

@media screen and (max-width:768px) {
    .navbar-principal {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 20px;
        padding-right: 15px;
        padding-bottom: 25px;
        background-color: #303030;
        height: auto;
    }
    .navbar .navbar-logo {
        left: 10px;
    }
    .navbar-pages {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;
        gap: 20px;
        width: auto;
    }
}

